@keyframes rotate-left{
  0% {
    transform: rotate(0deg);
    transform-origin: 11px 0px;
  }
  100% {
    transform: rotate(-45deg);
    transform-origin: 11px 0px;
  }
}

@keyframes rotate-left-reverse{
  0% {
    transform: rotate(-45deg);
    transform-origin: 11px 0px;
  }
  100% {
    transform: rotate(0deg);
    transform-origin: 11px 0px;
  }
}

@keyframes rotate-right{
  0% {
    transform: rotate(0deg);
    transform-origin: 11px 9px;
  }
  100% {
    transform: rotate(45deg);
    transform-origin: 11px 9px;
  }
}

@keyframes rotate-right-reverse{
  0% {
    transform: rotate(45deg);
    transform-origin: 11px 9px;
  }
  100% {
    transform: rotate(0deg);
    transform-origin: 11px 9px;
  }
}

.anime-top-line{
  animation: rotate-left 0.5s forwards;
}

.anime-bottom-line{
  animation: rotate-right 0.5s forwards;
}

.anime-bottom-line-reverse{
  animation: rotate-right-reverse 0.5s forwards;
}

.anime-top-line-reverse{
  animation: rotate-left-reverse 0.5s forwards;
}

@keyframes extend-menu{
  0%{
    height: 59px
  }
  100%{
    height: 100vh;
  }
}

@keyframes extend-menu-reverse{
  0%{
    height: 100vh;
  }
  100%{
    height: 59px;
  }
}


.anime-extend-menu{
  animation: extend-menu 0.3s forwards;
}

.anime-extend-menu-reverse{
  animation: extend-menu-reverse 0.3s forwards;
}